body {
  font-family: 'Jost', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: background-color 0.5s, color 0.5s;
}

.dark-mode {
  background: linear-gradient(135deg, #0c0c0c, #141414, #1c1c1c, #222222); /* Dark gradient background */
  color: #e0e0e0; /* Light text for contrast */
}

.light-mode {
  background: linear-gradient(135deg, #f0f0f0, #e6e6e6, #dcdcdc, #d3d3d3); /* Light gradient background */
  color: #333333; /* Dark text for contrast */
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}