/* Dropdown.css */
.dropdown {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    z-index: 1;
  }
  
  .dropdown-toggle {
    font-size: 20px;
  }
  
  .dropdown-content {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 30px;
    right: 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    transition: background-color 0.3s;
  }
  
  .dropdown-item {
    padding: 15px;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .dropdown-item:hover {
    background-color: #f0f0f0;
  }
  
  /* Light Mode */
  .light-mode .dropdown-content {
    background: linear-gradient(135deg, #f0f0f0, #e6e6e6, #dcdcdc, #d3d3d3);
    color: #282c34; /* Adjust text color accordingly */
  }
  
  /* Dark Mode */
  .dark-mode .dropdown-content {
    background: linear-gradient(135deg, #0c0c0c, #141414, #1c1c1c, #222222);
    color: #ffffff; /* Adjust text color accordingly */
  }
  